@import url(https://fonts.googleapis.com/css?family=Righteous|Shadows+Into+Light|BenchNine&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

h1,
h2 {
  font-family: "Righteous", cursive; }

body {
  margin: 0;
  background-color: #a3c577; }

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 20vh;
  color: black; }

.nav-links {
  width: 80%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none; }

/* HERO IMAGE */
.hero {
  /* Sizing */
  width: 100vw;
  height: 100vh;
  /* Flexbox stuff */
  display: flex;
  justify-content: center;
  text-align: center;
  margin-bottom: 0.5em;
  background-image: url(https://images.unsplash.com/photo-1554825203-68321ddde262?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1877&q=80);
  background-size: cover;
  background-position: center center; }
  .hero h1 {
    margin: 15% 0 90% 0;
    /* Text styles */
    font-size: 3em;
    color: #654512;
    text-shadow: 2px 2px #f8f05d; }
  .hero h2 {
    margin: 0 0 80% 0; }

/* END HERO IMAGE */
nav li {
  font-family: "Righteous", cursive;
  font-size: 2em;
  color: #654512;
  text-shadow: 2px 2px #f8f05d; }

form {
  margin-bottom: 2%; }

li {
  list-style-type: none; }

a :hover {
  color: #f8f05d;
  text-shadow: 2px 2px #654512; }

.vote h3 {
  display: inline-block; }

.vote img {
  width: 3%;
  height: 3%;
  margin-left: 10px; }

.news {
  font-family: "Shadows Into Light", cursive;
  font-size: 1.5em; }
  .news h1 {
    text-shadow: 0 1px 0 #d99528; }
  .news h4 {
    margin-bottom: 2%; }
  .news ul {
    margin: 0;
    padding: 0;
    list-style: none; }
    .news ul li {
      background-color: #f8f05d;
      color: #3a270a;
      margin: 5% 10%;
      border: 5px #654512 solid;
      border-radius: 5px;
      padding: 2%; }
      .news ul li:before {
        display: inline-block;
        color: #e3b060;
        padding: 2px;
        border-radius: 0.5px;
        float: left;
        clear: left;
        text-shadow: -1px -1px 0 #3a270a;
        padding: 2px 10px 0 0; }
  .news a {
    color: #654512;
    text-shadow: -0.5px 0 #a3c577, 0 0.5px #a3c577, 0.5px 0 #a3c577, 0 -0.5px #a3c577;
    display: block;
    margin: 3% 0;
    font-family: "BenchNine", sans-serif;
    text-decoration: none; }
    .news a:hover {
      background-color: #654512;
      border-radius: 5px;
      padding: 0.5%;
      color: #a3c577;
      text-shadow: -1px 0 #654512, 0 1px #654512, 1px 0 #654512, 0 -1px #654512; }
  .news .thumbnail {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 25%;
    margin-bottom: 10px; }

form input[type="text"] {
  width: 100%;
  margin: 2% 0; }

form button {
  border: 5px solid #a3c577;
  border-radius: 5px;
  background-color: #654512;
  color: #f8f05d;
  margin: 2% 0; }
  form button:hover {
    border: 5px solid #654512;
    background-color: #a3c577;
    color: #654512; }

@media (min-width: 768px) {
  .hero h1 {
    font-size: 4em;
    word-spacing: 20%;
    margin: 10% 0 35% 0; }
  .hero h2 {
    font-size: 1.5em;
    margin: 0 0 40% 0; } }

@media (min-width: 768px) {
  .news a {
    display: inline;
    margin: 0 1%; } }

